<template>
    <div class="">
        <table class="table table-top-line-none">
            <tr>
                <td>
                    發票編號
                </td>
                <td>
                    發票時間
                </td>
                <td class="">
                    客人
                </td>
                <td class=" ">
                    總價
                </td>
                <td>
                    付款期限
                </td>
                <td class="text-center">
                    付款狀態
                </td>
                <td class="text-center">
                    備註
                </td>
                <td class="text-center">
                    操作
                </td>
            </tr>

            <tr v-for="(v, i) in listings" :key="i">
                <td>
                    {{ v.num }}
                </td>
                <td>
                    {{ ser_timed(v.listing_time) }}
                </td>
                <td class="">
                    {{ v.membery.num }}&nbsp;{{ v.membery.named }}
                </td>
                <td class="">
                    ${{ v.result_price }}
                </td>
                <td>
                    {{ v.pay_time.named }}
                </td>
                <td class="text-center" v-html="pay_status(v.pay_status)">

                </td>
                <td class="text-center fa-pos-fix-table">
                    <i class="fa fa-edit"></i>
                </td>
                <td class="text-center">
                    <i class="float-right">
                        <remove></remove>
                    </i>

                    <i class="float-right">
                        <pencil></pencil>
                    </i>

                </td>
            </tr>
        </table>
    </div>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import conn from '@/service/conn'

import Loading from '@/components/Shimmer/Loading.vue'
import Pencil from '@/components/Option/Pencil.vue'
import Remove from '@/components/Option/Remove.vue'
    export default {
        name: '',
        components: { Loading, Pencil, Remove },
        props: {
            member: {
                type: Object
            }
        },
        data() {
            return {
                listings: []
            }
        },
        mounted() {
            this.loadListing()
        },
        methods: {
            inList(id, list) {
                return tool.inList(id, list)
            },
            pay_status(v) { return view.ser_pay_status(v) },
            ser_timed(v) {return tool.timed.ser_timed(v, true, true) },
            ser_timed_now_month(v, id) { 
                const res = tool.timed.is_now_month(v)
                
                if (res) {
                    return `<div class="tip tip-success fw-b">!</div>`
                    // return `<span class="tip-now-month">本月</span>`
                } 
                return ''
            },
            async loadListing() {
                const res = await conn.listing.getList_Listing({

                }, true)
                
                if (res) {
                    this.listings = res
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/ready.sass"
</style>


